.intro-overlay {
    .top {
        position: absolute;
        width: 100%;
        height: 50vh;
        height: calc(var(--vh, 1vh) * 50);
        z-index: 200;


        .overlay-top {
            background-color: $black;
            height: 100%; 
            width: 100%; 
            position: absolute;
            bottom: 0;
            left: 0;

            @media(min-width: 768px){
                width: 33.4%;
            }

            &:nth-child(2){
                display: none;

                @media(min-width: 768px){
                    display: block;
                    left: 33.333%;
                }
            }

            &:nth-child(3){
                display: none;

                @media(min-width: 768px){
                    display: block;
                    left: 66.666%;
                }
            }
        }
    }

    .bottom {
        width: 100%;
        height: 50vh;
        height: calc(var(--vh, 1vh) * 50);
        position: absolute;
        bottom: 0;
        z-index: 200;

        .overlay-bottom {
            background-color: $black;
            width: 100%;
            height: 100%;
            position: absolute;
            right: 0;

            @media(min-width: 768px){
                width: 33.4%;
                right: 66.6666%;
            }

            &:nth-child(2){
                top: 100%;

                @media(min-width: 768px){
                    top: 0;
                    right: 33.3333%;
                }
            }

            &:nth-child(3){
                top: 200%;

                @media(min-width: 768px){
                    top: 0;
                    right: 0;
                }
            }
        }
    }
}

